import * as path from 'path';

import {
  BaseGenerator,
  AvailableFrameworks
} from '../../common/BaseGenerator';
import * as Utils from '../../common/utilities';

import * as BaseExtension from '../extension/BaseExtensionGenerator';

export interface ICommandSetOptions extends BaseExtension.IBaseExtensionOptions {}
export interface ICommandSetContext extends BaseExtension.IBaseExtensionContext {}

// tslint:disable-next-line:no-any
export function composeWith(base: BaseGenerator<any, any>, options: ICommandSetOptions): void {
  Utils.compose(path.basename(__dirname), base, options);
}

// tslint:disable-next-line:no-any
export function defineOptions(generator: BaseGenerator<any, any>): void {
  BaseExtension.defineOptions(generator, 'Command Set');
}

export class CommandSetGenerator extends BaseExtension.BaseExtensionGenerator<ICommandSetOptions, ICommandSetContext> {
  protected readonly friendlyName: string = 'Command Set';
  protected readonly codeName: string = 'CommandSet';
  protected readonly allowedFrameworks: AvailableFrameworks[] = [];

  public initializing(): void { /* no-op */ }

  public prompting(): Promise<void> {
    return super.prompting();
  }

  public configuring(): void {
    super.configuring();
    this.ensureDependencyGroup('commandSet');

    this.serveJson.addConfiguration(this.context.componentNameCamelCase, {
      pageUrl: 'https://contoso.sharepoint.com/sites/mySite/SitePages/myPage.aspx',
      customActions: {
        [this.context.componentId]: {
          location: 'ClientSideExtension.ListViewCommandSet.CommandBar',
          properties: {
            sampleTextOne: 'One item is selected in the list',
            sampleTextTwo: 'This command is always visible.'
          }
        }
      }
    });
  }

  public writing(): void {
    super.writing();
  }

  public install(): void {
    super.install();
  }

  public end(): void { /* no-op */}

  // tslint:disable-next-line:no-any
  protected constructor(args: string | string[], options: any) {
    super(args, options);
    defineOptions(this);
  }
}

// Yeoman expects exports of this type, which overrides the TS exports above
// Ensure that anything marked as export above is also listed here
module.exports = CommandSetGenerator;
module.exports.defineOptions = defineOptions;
module.exports.composeWith = composeWith;
